:host {
  display: block;
}

.chat-input-container {
  display: flex;
  align-items: flex-end;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  background-color: var(--card-background-color, #ffffff);
  border-top: 1px solid var(--input-border-color, #e2e8f0);
  border-radius: 0 0 0.5rem 0.5rem;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.04);
}

.input-wrapper {
  flex: 1;
  position: relative;
}

.chat-textarea {
  width: 100%;
  min-height: 2.5rem;
  max-height: 150px;
  padding: 0.625rem 1rem;
  font-size: 0.9375rem;
  line-height: 1.5;
  color: var(--input-font-color, #1e293b);
  background-color: var(--input-background-color, #ffffff) !important;
  border: 2px solid var(--color-primary-color, #3b82f6);
  border-radius: 1.25rem;
  resize: none;
  overflow-y: auto;
  transition:
    border-color 0.2s ease,
    box-shadow 0.2s ease,
    background-color 0.2s ease;

  &:focus {
    outline: none;
    background-color: var(--input-background-color, #ffffff) !important;
    border-color: var(--color-primary-color, #2563eb);
    box-shadow: 0 0 0 3px rgba(var(--color-primary-rgb, 59, 130, 246), 0.2);
  }

  &:disabled {
    background-color: var(--input-background-color, #f1f5f9) !important;
    color: var(--input-placeholder-color, #64748b);
    border-color: var(--input-border-color, #cbd5e1);
    cursor: not-allowed;
    opacity: 0.7;
  }

  &::placeholder {
    color: var(--input-placeholder-color, #94a3b8);
  }
}

.send-button {
  flex-shrink: 0;
  width: 2.5rem;
  height: 2.5rem;
  padding: 0;
  border-radius: 50%;
  background: var(--color-primary-color, #3b82f6) !important;
  border: none;
  color: var(--color-primary-contrast, #ffffff);
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover:not(:disabled) {
    filter: brightness(0.9);
    transform: scale(1.05);
  }

  &:active:not(:disabled) {
    transform: scale(0.98);
  }

  &:disabled {
    background: var(--input-border-color, #d1d5db) !important;
    cursor: not-allowed;
    transform: none;
    opacity: 0.6;
  }

  ::ng-deep .p-button-icon {
    font-size: 1rem;
  }
}
